import Box from 'components/Box'
import theme from 'components/ThemeProvider/theme'
import React from 'react'

const PageTitle = ({ children }) => {
  return (
    <Box
      position="absolute"
      top={55}
      left={64}
      transform="translateY(-50%)"
      fontSize="54px"
      color="darkGray"
      fontWeight="900"
      whiteSpace="nowrap"
      fontFamily={theme.font}
    >
      {children}
    </Box>
  )
}

export default PageTitle
