import React, { useCallback, useMemo } from 'react'
import styled from 'styled-components'
import Box from 'components/Box'
import Flex from 'components/Flex'

const Timeline = styled(Flex)`
  top: 0;
  bottom: 0;
  position: absolute;
  ${props => props['data-extended'] === 'true' && `
    &::after {
      content: "";
      position: absolute;
      display: block;
      top: 0;
      right: -${props.height}px;
      border-width: ${props.height / 2}px;
      border-style: solid;
      border-color: transparent transparent transparent ${props.bg};
    }
  `}
`

const DramTimeline = ({ data, startYear, startQuarter, yearSpan, colors, thickness = 32, children }) => {
  const getPosition = useCallback((d, isEnd = 0) => {
    if (isEnd && !d) return 101
    if (d.year < startYear) return 0
    const q = (d.year - startYear) * 4 + ((d.month + isEnd) / 3 - startQuarter + 1)
    return Math.max(0, q / (yearSpan * 4) * 100)
  }, [startYear, startQuarter, yearSpan])
  return (
    <Box
      position="absolute"
      left="0"
      right="0"
      height={`${thickness}px`}
      top="50%"
      transform="translateY(-50%)"
    >
      {data && data.map((d, i) => {
        if (!d[0]) return null
        const start = getPosition(d[0])
        if (start > 100) return null
        const end = getPosition(d[1], 1)
        return (
          <Timeline
            key={i}
            bg={colors[i]}
            style={{
              left: `${start}%`,
              width: `${Math.min(100, end) - start}%`,
            }}
            height={thickness}
            data-start={d[0].str}
            data-end={d[1].str}
            data-extended={String(end > 100)}
            fontSize="27.5px"
            color="white"
            fontWeight="bold"
            whiteSpace="nowrap"
            justifyContent="flex-end"
            alignItems="center"
          >
            {children && (
              <Box pr="1em">{children}</Box>
            )}
          </Timeline>
        )
      })}
    </Box>
  )
}

export default DramTimeline
