import React, { useState, cloneElement, forwardRef, useRef, useEffect } from 'react'
import Measure from 'react-measure'
import { saveAs } from 'file-saver'

import Box from 'components/Box'
import bg from './RenderTable/bg.png'
import BackgroundImage from 'components/BackgroundImage'
import Dom2Canvas from 'utils/dom2canvas'
import Button from 'components/Button'

const PageSimulator = forwardRef(({ children, width = 1920, height = 1080, name, downloadRef }, ref) => {
  const [isDownloading, setIsDownloading] = useState(false)
  const [scale, setScale] = useState(1)
  const wrapperRef = useRef()

  const handleDownload = () => new Promise((res) => {
    setIsDownloading(true)
    const date = new Date().toLocaleDateString()
    const ins = new Dom2Canvas(wrapperRef.current, width, height)
    ins.toCanvas((canvas) => {
      canvas.toBlob(blob => {
        saveAs(blob, `${date}-${name}.png`);
        setIsDownloading(false)
        res()
      })
    })
  })
  useEffect(() => {
    if (downloadRef) {
      downloadRef.current = handleDownload
    }
  }, [downloadRef])
  return (
    <>
      <Measure
        bounds
        onResize={contentRect => {
          setScale(contentRect.bounds.width / width)
        }}
      >
        {({ measureRef }) => <div ref={measureRef} />}
      </Measure>
      <Box pt={`${height / width * 100}%`} position="relative">
        <Box.FullAbsolute
          style={{
            transform: `scale(${scale})`,
            transformOrigin: '0 0'
          }}
          ref={ref}
        >
          <BackgroundImage
            position="relative"
            width={width}
            height={height}
            ref={wrapperRef}
            src={bg}
            pointerEvents="none"
            userSelect={process.env.NODE_ENV === 'production' && 'none'}
            fontFamily="'Noto Sans', sans-serif"
          >
            {cloneElement(children, { scale })}
          </BackgroundImage>
        </Box.FullAbsolute>
      </Box>
      <Button disabled={isDownloading} my="2em" onClick={handleDownload}></Button>
    </>
  )
})

export default PageSimulator
