const months = ["jan", "feb", "mar", "apr", "may", "jun", "jul", "aug", "sep", "oct", "nov", "dec"]

const quarterPttns = [
  /^([1-4])Q'?(\d{2})$/i,
  /^(\d{2})Q([1-4])$/i,
  /^Q([1-4])'?(\d{2})$/i,
]
const monthPttn = new RegExp(`(${months.join('|')})'?(\\d{2})`, 'i')
const quarterToMonth = (q, isEnd = 0) => (q - 1) * 3 + isEnd * 2

const yStart = 2000
const parseDate = (str, isEnd) => {
  let i = 0
  let match
  while (!match && i < quarterPttns.length) {
    match = str.match(quarterPttns[i])
    i++
  }
  if (match) {
    const [, q, y] = match
    if (q.length === 1) {
      return {
        str,
        year: +y + yStart,
        month: quarterToMonth(q, isEnd),
      }
    }
    return {
      str,
      year: +q + yStart,
      month: quarterToMonth(y, isEnd),
    }
  }
  match = str.match(monthPttn)
  if (match) {
    const [, m, y] = match
    return {
      str,
      year: +y + yStart,
      month: months.findIndex((month) => month === m.toLowerCase()),
    }
  }
  return str
}

export default parseDate
